<template>
  <div>
    <h1>{{ $t('cart') }}</h1>
    <table>
      <thead>
      </thead>
      <tbody>
        <LineItem v-for="item in lineItems" v-bind:key="item.productId" v-bind:item="item" />
      </tbody>
    </table>

    <div class="cart__sum">
      {{$t('currentSum')}}: {{ parseFloat(cartSum).toFixed(2) }}€
    </div>
  </div>
</template>

<script>
  import LineItem from "./LineItem.vue";

  export default {
    props: {
      lineItems: Array,
    },
    computed: {
    cartSum: function () {
       return this.lineItems.reduce((sum, currentItem)=> sum + currentItem.price, 0)      
      }
    },

    components: {
      LineItem
    }
  };
</script>
