<template>
  <div id="app">
    <Cart v-bind:lineItems="lineItems"></Cart>
    <CartNavButtons v-bind:checkoutURL="checkoutURL"></CartNavButtons>
    
  </div>
</template>

<script>
// <Header></Header>
// <Footer></Footer>
  import Cart from "./components/Cart.vue";
  import CartNavButtons from "./components/CartNavButtons.vue";
  import Footer from "./components/Footer.vue";
  import Header from "./components/Header.vue";
  
  const cart = {
    lineItems: [],
    checkoutURL: undefined
  };


  
  export default {
    name: "app",
    data: function() {
      return cart;
    },


    created: async function (){
      // in this form only temp
      // used to have something the server can return
      const mockLineItem = {       
        productType: 'starmap',
        productId: 'ByMl9lLOB7',
        lineItemProperties: { delivery: 'pdf' },       
      }
      const lng = window.lng
      const cartBaseUrl = process.env.VUE_APP_STARMAP_BACKEND_URL

      await fetch(cartBaseUrl+'/cart/'+mockLineItem.productId, {
        method: 'PUT',
        credentials: 'include',
        headers: {
          'Content-Type': 'application/json; charset=utf-8',
        },
        body: JSON.stringify(mockLineItem),
      })
      const mockLineItem2 = {       
        productType: 'starmap',
        productId: 'SylgPeu8KQ',
        lineItemProperties: { delivery: 'poster' },       
      }

      await fetch(cartBaseUrl+'/cart/'+mockLineItem2.productId, {
        method: 'PUT',
        credentials: 'include',
        headers: {
          'Content-Type': 'application/json; charset=utf-8',
        },
        body: JSON.stringify(mockLineItem2),
      })

      // asynchronously get checkout url
      fetch(cartBaseUrl+'/cart/checkout/'+lng, {
            method: 'GET',
            credentials: 'include',
          })
          .then((res) => res.text())
          .then((url) => {
            cart.checkoutURL = url
          })

      let getCartResponse = fetch(cartBaseUrl+'/cart/'+lng, {
            method: 'GET',
            credentials: 'include',
          })
          .then((response) => response.json())

      const cartFromServer = await getCartResponse
      cart.lineItems = Object.values(cartFromServer.lineItems)
    },

    components: {
      Cart,
      CartNavButtons,
      Header,
      Footer
    }
  };
</script>

<style>
  @import url('https://fonts.googleapis.com/css?family=Work+Sans:400,600&subset=latin-ext');

#app {
  color: #3d4246;
  font-family: 'Work Sans', sans-serif;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>
