<template>
  <div class="cart__nav-buttons">
    <a v-bind:href=newPosterUrl>
      <button class="button">
        {{$t('addProduct')}}
      </button>
    </a>
    <!-- TODO: GET cart/checkout -->
    <a v-bind:href=buyNowUrl v-bind:class="{ loading: checkoutURL === undefined }">
      <button class="button primary">
      {{$t('checkout')}} 
      </button>
    </a>
  </div>
</template>
<script>
  export default {
    name: "CartNavButtons",
    props: {
      checkoutURL: String,
    },
    computed: {
      newPosterUrl: function () {      
        return `${process.env.VUE_APP_STARMAP_EDITOR_URL}/poster/`
      },
      // TODO: This is just for testing
      buyNowUrl: function() {
        return `https://secure.nachthimmel.eu.yoursky.space${this.checkoutURL}`
      },
    },
  };
</script>

<style>
