<template>
<tbody>
  <tr>
    <td>
      <!-- TODO: add loading indicator -->
      <img v-bind:src=imageUrl v-on:click="showPreviewModal">
    </td>
    <td>
      <div>{{ titleKey }}</div>
      <div class="lineItem__editorProperties-container">
        <div v-for="(item, key) in item.editorProperties" v-bind:key="key">
          {{ key }}: {{ item }}
        </div>
      </div>
    </td>
    <td>
      {{ parseFloat(item.price).toFixed(2)}}€
    </td>
  </tr>
  <tr>
    <td colspan="3" >
      <div class="lineItem__button--container">
        <button v-on:click="showPreviewModal" class="button primary">
          {{$t('preview')}}
        </button>
        <a v-bind:href=modifyPosterUrl>
          <button class="button">
            {{$t('edit')}}
          </button>
        </a>
        <button class="button">
          {{$t('delete')}}
        </button>
      </div>
    </td>
    <PreviewModal v-if="isPreviewModalVisible" v-bind:imageUrl="imageUrl" @close="closePreviewModal">></PreviewModal>
  </tr>
</tbody>
</template>


<script>
import PreviewModal from "./PreviewModal.vue"

export default {
  name: 'LineItem',
  props: {
    item: Object,
  },
  data () {
      return {
        isPreviewModalVisible: false,
      };
    },
  methods: {
      showPreviewModal() {
        this.isPreviewModalVisible = true;
      },
      closePreviewModal() {
        this.isPreviewModalVisible = false;
      }
    },
  computed: {
      imageUrl: function () {      
        //todo adjust for different product names
      return `${process.env.VUE_APP_STARMAP_BACKEND_URL}/posters/${this.item.productId}/preview.jpg`
      },
      modifyPosterUrl: function () {
        // This productId thing is just a temp solution to support legacy
        const shopifyProductId = (this.item.lineItemProperties.delivery === 'PDF') ? '1030596624428' : '7030690480172'
        return `${process.env.VUE_APP_STARMAP_EDITOR_URL}/poster/?edit=true&productId=${shopifyProductId}&posterId=${this.item.productId}`
      },
      titleKey: function () {
        // this means that for now every line item property has to change the title. Also it is unclear if the ordering will work like this. We have to see how this works for more complicated products
        const properties = Object.values(this.item.lineItemProperties).join('.')
        return this.$t(this.item.productType+':product.'+properties+'.title')
      }
    },
  components: {
     PreviewModal
    },
}
</script>

